button {
    border: none;
    background-color: rgb(80, 60, 209);
    font-size: 24px;
    color: rgb(255, 255, 255);
    font-weight: bold;
    cursor: pointer;
    border-radius: 10px;
    outline: none;
  }
  
  button:hover {
    background-color: rgb(61, 43, 184);
  }

  #clear {
    grid-column: 1 / 3;
    background-color: rgb(243, 61, 29);
  }
  
  #clear:hover {
    background-color: rgb(228, 39, 15);
  }

  #zero {
    grid-column: 1 / 3;
  }

  #equals {
    grid-column: 4 / 5;
    grid-row: 4 / 6;
    background-color: rgb(243, 61, 29);
  }
  
  #equals:hover {
    background-color: rgb(228, 39, 15);
  }